import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { Button, Container, Form, FormGroup, Input, Label } from 'reactstrap';
import {connect} from "react-redux";

class ReportPage extends Component {

    emptyReport = {
        userLogin: '',
        firstName: '',
        lastName: '',
        occupation: '',
        date: '',
        description: '',
        isReviewed: ''
    };

    constructor(props) {
        super(props);
        this.state = {
            reportBody: this.emptyReport,
            date: ''
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    async componentDidMount() {
        let occupationId = this.props.match.params.occupationId;
        const colonist = await (await fetch('/api/colonist/' + this.props.user)).json();
        const occupation = await (await fetch('/api/occupation/' + occupationId)).json();
        JSON.stringify(colonist);
        JSON.stringify(occupation);
        let today = new Date(),
            date = (today.getDay() < 10 ? ('0'+today.getDay()) : today.getDay())+ '.'
                + (today.getMonth()+1< 10 ? '0'+(today.getMonth()+1) : today.getMonth()+1) + '.'
                + today.getFullYear();
        let report = {
            userLogin: this.props.user,
            firstName: colonist.firstName,
            lastName: colonist.lastName,
            occupation: occupation.name,
            description: '',
            isReviewed: 'false'
        };
        this.setState({reportBody : report, date: date});
    }

    handleChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        let reportBody = {...this.state.reportBody};
        reportBody[name] = value;
        this.setState({reportBody});
    }

    async handleSubmit(event) {
        event.preventDefault();
        const {reportBody} = this.state;

        await fetch('/api/report/', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(reportBody),
        });
        this.props.history.push('/work');
    }

    render() {
        const {reportBody, date} = this.state;

        return <div>
            <Container>
                <div className="main-wrapper"><div className="Wrapper2"><div className="main">
                    <h3>Report form:</h3>
                <Form onSubmit={this.handleSubmit}>
                    <FormGroup>
                        <Label for="firstName">
                            : {reportBody.firstName}
                        </Label>
                    </FormGroup>
                    <FormGroup>
                        <Label for="lastName">
                            : {reportBody.lastName}
                        </Label>
                    </FormGroup>
                    <FormGroup>
                        <Label for="occupation">
                            : {reportBody.occupation}
                        </Label>
                    </FormGroup>
                    <FormGroup>
                        <Label for="description">Detailed report of the work day:</Label><br/>
                        <Input type="textarea" name="description" id="report-description" value={reportBody.description || ''}
                               onChange={this.handleChange}/>
                    </FormGroup>
                    <FormGroup>
                        <Label for="date">
                            : {date}
                        </Label>
                    </FormGroup>
                    <FormGroup>
                        <Button color="primary" type="submit"></Button>{' '}
                        <Link to={'/work'}><Button></Button></Link>
                    </FormGroup>
                </Form>
                </div></div></div>
            </Container>
        </div>
    }
}

const mapStateToProps = function(store) {
    return {
        user: store.appState.user,
    }
};

export default connect(mapStateToProps)(ReportPage);
